export default {
	name: 'cu-cell',
	props: {
		title: {
			type: String,
			default: ''
		},
		disabled: {
			type: Boolean,
			default: false
		},
		isIcon: {
			type: Boolean,
			default: false
		},
		icon: {
			type: Object,
			default: () => {
				return {
					type: '',
					color: '',
					size: 0
				}
			}
		},
		isLink: {
			type: Boolean,
			default: false
		},
		url: {
			type: String,
			default: ''
		},
		params: {
			type: String,
			default: ''
		},
		isLastCell: {
			type: Boolean,
			default: false
		},
		isSub: {
			type: Boolean,
			default: false
		},
		notes: {
			type: String,
			default: ''
		},
		notesColor: {
			type: String,
			default: ''
		},
		disVerMessage: {
			type: Boolean,
			default: false
		},
		verify: {
			type: String,
			default: ''
		},
		verifyColor: {
			type: String,
			default: ''
		},
		notNull: {
			type: Boolean,
			default: false
		},
		isExtendIcon: {
			type: Boolean,
			default: false
		},
		extendIcon: {
			type: Object,
			default: () => {
				return {
					type: '',
					color: '',
					size: 0
				}
			}
		},
		isExtendText: {
			type: Boolean,
			default: false
		},
		extendText: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
		}
	},
	methods: {
		navigateTo () {
			let url = this.url
			if (this.params) {
				url += '?'+this.params
			}
			uni.navigateTo({
				url: url
			})
		},
		handleCell () {
			if (this.isLink) {
				this.navigateTo()
			} else {
				this.$emit('clickCell')
			}
		},
		handleBd () {
			this.$emit('clickTitle')
		},
		handleExtend () {
			this.$emit('clickExtend')
		}
	}
}
